
import * as types from './mutations_types';

export default module.exports = {
    clear_local: ({
        commit
    }) => {
        commit(types.CLEAR_LOCAL);
    },
    update_local: ({
        commit
    }) => {
        commit(types.UPDATE_LOCAL);
    },
    delete_db: ({
        commit
    }) => {
        commit(types.DELETE_DB);
        commit(types.UPDATE_LOCAL);
    },
    create_db: ({
        commit
    }, {
        shop
    }) => {
        commit(types.CREATE_DB, shop);
        commit(types.UPDATE_LOCAL);
    },
    add_db: ({
        commit
    }) => {
        commit(types.ADD_DB);
        commit(types.UPDATE_LOCAL);
    },
    reduce_db: ({
        commit
    }) => {
        commit(types.REDUCE_DB);
        commit(types.UPDATE_LOCAL);
    },
    check_db: ({
        commit
    }, obj) => {
        commit(types.CHECK_DB, obj);
    },
    get_walletState: ({
        commit
    }, obj) => {
        commit(types.CHECK_WalletState, obj);
    },connect_wallet: ({
        commit
    }, obj) => {
        commit(types.CONNECT_WALLET, obj);
    },unsubscribe_wallet: ({
        commit
    }, obj) => {
        commit(types.UNSUBSCRIBE_WALLET, obj);
    },activate_account: ({
        commit
    }, obj) => {
        commit(types.ACTIVATE_ACCOUNT, obj);
    },get_user_infos: ({
        commit
    }, obj) => {
        commit(types.CHECK_USER, obj);
    },check_referralid: ({
        commit
    }, obj) => {
        commit(types.CHECK_REFERRALID, obj);
    }
};


