        if (this.twitterState == 1) {
          var toTwitterAccount = this.masterTwitter;
        }else{
          var toTwitterAccount = this.twitterAccount;
        }
        //referralIDState only have 0/1
        if (this.referralIDState == 1) {var toReferralIDValue = this.fromUrlReferralID;}//in referralID msg
        if (this.referralIDState == 0){var toReferralIDValue = this.referralIDValue;} //in input
        
        // var toReferralIDValue = this.referralIDValue;

        var obj = {
          "toAccount":toAccount,
          "toTwitterAccount":toTwitterAccount,
          "toReferralIDValue":toReferralIDValue
        }
        // console.log("lastea xxx...,",obj);
        // return 0;
        this.$store.dispatch('activate_account',obj)
        
      },refreshReferralID(){
