import axios from 'axios';
import qs from 'Qs'

module.exports = {
    getUserInfos(state,obj) {
        var toAccount = state.account;
        //console.log('res=>toAccount...',state,toAccount)
        let data = {
            "account":toAccount
        };
        axios.post('https://mmmfi.app/api/userInfo',qs.stringify({data}))
        .then(function (response) {
            // console.log("userInfo response... ",response.data.result);
            var resUserData = response.data.result;
            var arrUserData = Object.keys(resUserData); 
          
            if(resUserData && arrUserData.length > 0){
                state.masterTwitter = resUserData.masterTwitter;
                state.masterReferralID = resUserData.masterReferralID;
                state.masterReferralLink = resUserData.masterReferralLink;
                state.referralData = resUserData.referralUserData ;
                var masterTwitter = resUserData.masterTwitter;
                if (masterTwitter.length>0) { //have twitter
                    state.twitterState = 1;
                }else{//not have twitter
                    state.twitterState = 0;
                }
                // console.log('res=>9',masterTwitter,masterTwitter.length)
            }else{
                // state.accountState = 10;
                // console.log('null=>resUserData')
                return 0;
            }
            
            
        })
        .catch(function (error) {
            // console.log("userInfo response error...",error);
        });
       
       
    },
    activateAccount(state,resultData){
        // console.log("s...",state);
        var toAccount = state.toAccount;
        var toTwitterAccount = state.toTwitterAccount;
        var toReferralIDValue = state.toReferralIDValue;
        //console.log("...1",toAccount,toTwitterAccount,toReferralIDValue);
        let data = {
            "account":toAccount,
            "twitterAccount":toTwitterAccount,
            "referralID":toReferralIDValue
        };
        // axios.post(`api/register`,qs.stringify({data}))
        // .then(res=>{
        //     console.log('res=>0',qs);      
        //     console.log('res=>1',res);       
        // })
        // console.log('res=>0x',qs);      
        // console.log('res=>1x',res); 
        axios.post('https://mmmfi.app/api/register',qs.stringify({data}))
        .then(function (response) {
            // console.log("9response...",response.data.result);
            if (response.data.result == 4) {

                state.activateAccountState = 4;//user have exist change state
                return 4 ;
            }else if(response.data.result == 1){

                state.activateAccountState = 1;
                return 1;
            }else if(response.data.result == 5){
                
                state.activateAccountState = 5;
                return 5;
            }else if(response.data.result == 6){
                
                state.activateAccountState = 6;
                return 6;
            }else if(response.data.result == 8){
                
                state.activateAccountState = 8;
                return 8;
            }
            else{
                console.log("err");
            }
            
        })
        .catch(function (error) {
            console.log("error...",error);
        });
      
        
        
    }


}



