  .bg-purple-dark {
    background: #99a9bf;
  }
 
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  .tt{
    width: 100%;
    height:10em;
    background-color:red;
  }
</style>





