        state. masterReferralLink ='';
        state.referralData = [{
          time:'',
          fromAccount:'',
          fromTwitter: '',
          lotteryNumbers:'',
          state:'10'
        }];

      //   async function Unsubscribe_Wallet() {

      //   const resUnsubscribe = await window.ethereum.request({ method: 'eth_unsubscribe',"params":["0xe11f438c7c982c50c6c6991cd021772386413050"]})

